body {
	background-color: #111;
	color: #eee;
  font-family: "Muli", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	margin: 2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4 {
	font-weight: bold;
}

.linkButton {
	-moz-box-shadow: 0px 0px 6px 0px #4c4f4b;
	-webkit-box-shadow: 0px 0px 6px 0px #4c4f4b;
	box-shadow: 0px 0px 6px 0px #4c4f4b;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #243027), color-stop(1, #474747));
	background:-moz-linear-gradient(top, #243027 5%, #474747 100%);
	background:-webkit-linear-gradient(top, #243027 5%, #474747 100%);
	background:-o-linear-gradient(top, #243027 5%, #474747 100%);
	background:-ms-linear-gradient(top, #243027 5%, #474747 100%);
	background:linear-gradient(to bottom, #243027 5%, #474747 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#243027', endColorstr='#474747',GradientType=0);
	background-color:#243027;
	-moz-border-radius:5px;
	-webkit-border-radius:5px;
	border-radius:5px;
	border:1px solid #757575;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:12px 25px;
	text-decoration:none;
	text-shadow:0px 1px 1px #90998e;
}
.linkButton:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #474747), color-stop(1, #243027));
	background:-moz-linear-gradient(top, #474747 5%, #243027 100%);
	background:-webkit-linear-gradient(top, #474747 5%, #243027 100%);
	background:-o-linear-gradient(top, #474747 5%, #243027 100%);
	background:-ms-linear-gradient(top, #474747 5%, #243027 100%);
	background:linear-gradient(to bottom, #474747 5%, #243027 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#474747', endColorstr='#243027',GradientType=0);
	background-color:#474747;
}
.linkButton:active {
	position:relative;
	top:1px;
}

.App>div {
  -moz-border-radius:5px;
  -moz-box-shadow:0px 0px 59px 8px #000000 ;
  -webkit-border-radius:5px;
  -webkit-box-shadow:0px 0px 59px 8px #000000 ;
  background:none repeat 0 0 rgba(255, 255, 255, 0.333);
  border-radius:5px;
  box-shadow:0px 0px 59px 8px #000000 ;
  padding: 20px;
}

#buttons {
	text-align: center;
}

a {
	color: #ccc;
	font-weight: 600;
	text-decoration: underline;
}

div p {
	font-size: 1.4em;
}

h1, h2, h3 {
	text-shadow: .1em .1em .1em #777;
}

h2 {
	font-size: 1.8em;
}

h3 {
	font-size: 1.6em;
}